<template>
  <Layout>
    <BRow>
      <BCol lg="12">
        <BCard no-body id="configList">
          <!-- Header -->
          <BCardHeader class="border-0">
            <BRow class="align-items-center gy-3">
              <BCol cols="sm">
                <h5 class="card-title mb-0">Settings | Attributes</h5>
              </BCol>
              <!-- <BCol sm="auto">
                <div class="d-flex gap-1 flex-wrap">
                  <BButton
                    type="button"
                    variant="success"
                    class="add-btn"
                    @click="openModal('')"
                  >
                    <i class="ri-add-line align-bottom me-1"></i> Add Config
                  </BButton>
                </div>
              </BCol> -->
            </BRow>
          </BCardHeader>

          <!-- Filters -->
          <BCardBody
            class="border border-dashed border-end-0 border-start-0 mb-2"
          >
            <BForm>
              <BRow class="g-3">
                <BCol xxl="3" sm="6">
                  <div class="search-box">
                    <input
                      type="text"
                      class="form-control search"
                      v-model="searchQuery"
                      placeholder="Search..."
                    />
                    <i class="ri-search-line search-icon"></i>
                  </div>
                </BCol>

                <BCol xxl="2" sm="4">
                  <Multiselect
                    class="form-control"
                    v-model="searchStatus"
                    :close-on-select="true"
                    :searchable="true"
                    :options="[
                      { value: '', label: 'All' },
                      { value: '1', label: 'Active' },
                      { value: '0', label: 'Inactive' },
                    ]"
                  />
                </BCol>

                <BCol xxl="2" sm="4" class="d-flex gap-2">
                  <BButton
                    type="button"
                    variant="primary"
                    class="w-auto"
                    @click="fetchDataTable(1)"
                  >
                    <i class="ri-equalizer-fill me-1 align-bottom"></i> Filters
                  </BButton>

                  <BButton
                    type="button"
                    variant="danger"
                    class="w-auto"
                    @click="clearFilters"
                  >
                    <i class="ri-close-circle-line align-bottom"></i>
                  </BButton>
                </BCol>
              </BRow>
            </BForm>
          </BCardBody>

          <!-- Table -->
          <BCardBody class="pt-0">
            <div class="table-responsive table-card mb-1">
              <table class="table table-nowrap align-middle">
                <thead class="text-muted table-light">
                  <tr class="text-uppercase">
                    <th>No.</th>
                    <th>Name</th>
                    <th>Description</th>
                    <th>Status</th>
                    <th>Created At</th>
                    <th class="text-center">Action</th>
                  </tr>
                </thead>

                <tbody>
                  <!-- Loading -->
                  <tr v-if="isLoading">
                    <td colspan="6" class="text-center">
                      <lottie
                        class="avatar-xl"
                        :options="defaultOptions"
                        :height="75"
                        :width="75"
                      />
                      <h5 class="mt-2">Loading...</h5>
                    </td>
                  </tr>

                  <!-- Table Content -->
                  <template v-else>
                    <tr v-for="(data, index) in tableData" :key="data.id">
                      <td>{{ (page - 1) * perPage + index + 1 }}</td>
                      <td>
                        <i :class="[data.icon, 'me-1', 'align-bottom']"></i>
                        {{ data.name }}
                      </td>
                      <td>{{ data.description }}</td>
                      <td>
                        <span
                          :class="
                            data.is_active
                              ? 'badge bg-success-subtle text-success fs-12'
                              : 'badge bg-danger-subtle text-danger fs-12'
                          "
                        >
                          {{ data.is_active ? "Active" : "Inactive" }}
                        </span>
                      </td>
                      <td>{{ formatShortDate(data.created_at) }}</td>
                      <td class="text-center">
                        <BDropdown
                          boundary="viewport"
                          variant="link"
                          toggle-class="btn btn-soft-secondary btn-sm arrow-none"
                          menu-class="dropdown-menu-end text-nowrap"
                        >
                          <template #button-content>
                            <i class="ri-more-fill align-middle"></i>
                          </template>
                          <BDropdownItem @click="openModal(data)">
                            <i class="ri-pencil-fill me-2 text-muted"></i>Edit
                          </BDropdownItem>
                          <BDropdownItem @click="openDeleteModal(data)">
                            <i class="ri-delete-bin-fill me-2 text-muted"></i
                            >Delete
                          </BDropdownItem>
                        </BDropdown>
                      </td>
                    </tr>

                    <tr v-if="!tableData.length">
                      <td colspan="6" class="text-center text-muted">
                        <h5 class="mt-2">No Result Found</h5>
                        <p class="mb-0">
                          We didn’t find any records for your search.
                        </p>
                      </td>
                    </tr>
                  </template>
                </tbody>
              </table>
            </div>

            <!-- Pagination -->
            <PaginationComponent
              :currentPage="page"
              :perPage="perPage"
              :total="totalItems"
              @pageChange="fetchDataTable"
              @perPageChange="updatePerPage"
            />
          </BCardBody>
        </BCard>
      </BCol>

      <!-- Create & Update  -->
      <BModal
        v-model="isModalOpen"
        hide-footer
        :title="isEdit ? 'Edit Config' : 'Add Config'"
        class="v-modal-custom"
      >
        <form @submit.prevent="handleSubmit">
          <BRow class="g-3">
            <!-- Name -->
            <BCol lg="12">
              <label class="form-label">Name</label>
              <input
                type="text"
                v-model="form.name"
                class="form-control"
                placeholder="Enter name"
              />
            </BCol>

            <!-- Icon -->
            <BCol lg="12">
              <label class="form-label">Icon (CSS class)</label>
              <input
                type="text"
                v-model="form.icon"
                class="form-control"
                placeholder="e.g. ri-apps-2-line"
              />
              <small class="text-muted">
                Use RemixIcon class, e.g. <code>ri-briefcase-2-line</code>.
                <br />
                <router-link
                  :to="{ name: 'otherIcons' }"
                  class="text-primary fw-semibold"
                >
                  Browse Remix Icons
                </router-link>
              </small>
            </BCol>

            <!-- Description -->
            <BCol lg="12">
              <label class="form-label">Description</label>
              <textarea
                class="form-control"
                v-model="form.description"
                placeholder="Enter description"
                rows="3"
              ></textarea>
            </BCol>

            <!-- Status -->
            <BCol lg="12">
              <label class="form-label">Status</label>
              <div>
                <div class="form-check form-check-inline">
                  <input
                    class="form-check-input"
                    type="radio"
                    id="active"
                    :value="true"
                    v-model="form.is_active"
                  />
                  <label class="form-check-label" for="active">Active</label>
                </div>
                <div class="form-check form-check-inline">
                  <input
                    class="form-check-input"
                    type="radio"
                    id="inactive"
                    :value="false"
                    v-model="form.is_active"
                  />
                  <label class="form-check-label" for="inactive"
                    >Inactive</label
                  >
                </div>
              </div>
            </BCol>

            <!-- Buttons -->
            <BCol lg="12">
              <div class="hstack gap-2 justify-content-end">
                <BButton
                  type="button"
                  variant="light"
                  @click="isModalOpen = false"
                >
                  Close
                </BButton>

                <BButton type="submit" variant="primary" :disabled="isLoad">
                  <span v-if="isLoad" class="d-flex align-items-center">
                    <span
                      class="spinner-border spinner-border-sm me-2"
                      role="status"
                    >
                      <span class="visually-hidden">Loading...</span>
                    </span>
                    Saving...
                  </span>
                  <span v-else>
                    {{ isEdit ? "Update" : "Submit" }}
                  </span>
                </BButton>
              </div>
            </BCol>
          </BRow>
        </form>
      </BModal>

      <!-- ✅ Delete Confirmation Modal -->
      <BModal
        v-model="isDeleteModalOpen"
        hide-footer
        hide-header-close
        centered
        no-close-on-backdrop
        class="v-modal-custom"
      >
        <div class="modal-body text-center">
          <!-- Lottie Animation -->
          <lottie
            colors="primary:#f7b84b,secondary:#405189"
            trigger="loop"
            :options="deleteAnimationOptions"
            :height="130"
            :width="130"
          />

          <div class="mt-4">
            <h4 class="mb-3 text-danger">Confirm Delete</h4>
            <p class="text-muted mb-4">
              Are you sure you want to delete
              <strong>{{ deleteItem?.name }}</strong
              >?<br />
              This action cannot be undone.
            </p>

            <div class="hstack gap-2 justify-content-center">
              <BButton
                variant="link"
                class="fw-medium text-muted"
                @click="isDeleteModalOpen = false"
              >
                <i class="ri-close-line me-1 align-middle"></i> Cancel
              </BButton>

              <BButton
                type="button"
                variant="danger"
                :disabled="isLoad"
                @click="confirmDelete()"
              >
                <span v-if="isLoad" class="d-flex align-items-center">
                  <span
                    class="spinner-border spinner-border-sm me-2"
                    role="status"
                  >
                    <span class="visually-hidden">Loading...</span>
                  </span>
                  Loading...
                </span>
                <span v-else>Delete</span>
              </BButton>
            </div>
          </div>
        </div>
      </BModal>
    </BRow>
  </Layout>
</template>

<script setup>
import { ref, onMounted } from "vue";
import Multiselect from "@vueform/multiselect";
import "@vueform/multiselect/themes/default.css";
import Layout from "@/layouts/main.vue";
import Lottie from "@/components/widgets/lottie.vue";
import animationData from "@/components/widgets/msoeawqm.json";
import animationData2 from "@/components/widgets/tdrtiskw.json";

import PaginationComponent from "@/components/pagination.vue";
import { formatShortDate } from "@/helpers/useFormats";

import Toast from "@/helpers/Toast";
import {
  getSettingAttributes,
  createSettingAttribute,
  updateSettingAttribute,
  deleteSettingAttribute,
} from "@/api-request";

const isLoad = ref(false);
const isLoading = ref(false);
const perPage = ref(10);
const page = ref(1);
const totalItems = ref(0);

const searchStatus = ref("");
const searchQuery = ref("");
const tableData = ref([]);
const defaultOptions = { animationData };

const isModalOpen = ref(false);
const isEdit = ref(false);
const editId = ref(0);
const form = ref({
  id: null,
  name: "",
  icon: "",
  description: "",
  is_active: false,
});

const deleteAnimationOptions = { animationData: animationData2 };
const isDeleteModalOpen = ref(false);
const deleteItem = ref(null);

const fetchDataTable = async (pageNumber = 1) => {
  page.value = pageNumber;
  isLoading.value = true;
  try {
    const response = await getSettingAttributes({
      search: searchQuery.value,
      status: searchStatus.value,
      per_page: perPage.value,
      page: pageNumber,
    });

    const data = response.data;

    tableData.value = data.items || [];
    totalItems.value = data.total || 0;
  } catch (error) {
    console.error(error);
  } finally {
    isLoading.value = false;
  }
};

const handleSubmit = async () => {
  isLoad.value = true;

  try {
    await new Promise((resolve) => setTimeout(resolve, 1000));

    const response = editId.value
      ? await updateSettingAttribute(editId.value, form.value)
      : await createSettingAttribute(form.value);

    Toast.fire({
      icon: "success",
      title: response?.message || "Process completed successfully.",
    });

    fetchDataTable();
    isModalOpen.value = false;
  } catch (error) {
    console.error("Submit Error:", error);

    Toast.fire({
      icon: "error",
      title:
        error.response?.data?.message || "An error occurred while processing.",
    });
  } finally {
    isLoad.value = false;
  }
};

const confirmDelete = async () => {
  isLoad.value = true;

  try {
    await new Promise((resolve) => setTimeout(resolve, 1000));

    const response = await deleteSettingAttribute(deleteItem.value.id);

    Toast.fire({
      icon: "success",
      title: response?.message || "Process completed successfully.",
    });

    isDeleteModalOpen.value = false;
    fetchDataTable(page.value);
  } catch (error) {
    console.error(error);

    Toast.fire({
      icon: "error",
      title:
        error.response?.data?.message || "An error occurred while processing.",
    });
  } finally {
    isLoad.value = false;
  }
};

const clearFilters = () => {
  searchQuery.value = "";
  searchStatus.value = "";
  fetchDataTable(1);
};

const updatePerPage = (size) => {
  perPage.value = size;
  fetchDataTable(1);
};

const openModal = (data = null) => {
  if (data) {
    isEdit.value = true;
    editId.value = data.id;
    form.value = {
      id: data.id,
      name: data.name,
      icon: data.icon,
      description: data.description,
      is_active: data.is_active,
    };
  } else {
    isEdit.value = false;
    editId.value = 0;
    form.value = {
      id: null,
      name: "",
      icon: "",
      description: "",
      is_active: false,
    };
  }

  isModalOpen.value = true;
};

const openDeleteModal = (item) => {
  deleteItem.value = item;
  isDeleteModalOpen.value = true;
};

onMounted(() => fetchDataTable());
</script>
